<template>
  <a-form-model
    ref="form"
    :model="entity"
    layout="vertical"
    autocomplete="off"
    :rules="rules"
    class="h-father"
  >
    <g-body-form-layout>
      <!-- 搜索插槽 -->
      <template #search-card>
        <div class="search-card">
          <a-row :gutter="24">
            <a-col :span="4">
              <!-- 设备组 -->
              <a-form-model-item label="设备组" prop="deviceName">
                <g-select
                  v-model="entity.deviceName"
                  placeholder="请选择设备组"
                >
                </g-select>
              </a-form-model-item>
            </a-col>
            <!-- 设备 -->
            <a-col :span="4">
              <a-form-model-item label="设备" prop="deviceName">
                <g-select
                  v-model="entity.deviceName"
                  placeholder="请选择设备"
                >
                </g-select>
              </a-form-model-item>
            </a-col>
            <!-- 数采模板 -->
            <a-col :span="4">
              <a-form-model-item label="数采模板" prop="deviceName">
                <g-select
                  v-model="entity.deviceName"
                  placeholder="请选择数采模板"
                >
                </g-select>
              </a-form-model-item>
            </a-col>
            <a-col :span="4"> </a-col>
              <a-col :span="4"> </a-col>
              <a-col :span="4">
                <div
                  style="display: flex;
                  justify-content: flex-end;
                  align-items: center;
                  height: 100%;"
                >
                  <g-btn-group
                    :buttonLists="btnList"
                    @btnCallback="btnCallback($event)"
                  ></g-btn-group>
                </div>
              </a-col>
          </a-row>
          
        </div>
      </template>
      <!-- 展开插槽 -->
      <template #expand-card>
        <div class="expand-card">
          <g-collapse headerName="采集配置">
            <template #content>
              <a-row :gutter="24">
                <a-col :span="4">
                  <!-- 采集类型 -->
                  <a-form-model-item label="采集类型" prop="collectionType">
                    <a-select
                      v-model="entity.collectionType"
                      placeholder="请选择采集类型"
                      @change="handleCollectionTypeChange"
                    >
                      <a-select-option value="定时采集">定时采集</a-select-option>
                      <a-select-option value="触发采集">触发采集</a-select-option>
                      <a-select-option value="条件采集">条件采集</a-select-option>
                      <a-select-option value="手动采集">手动采集</a-select-option>
                      <a-select-option value="实时采集">实时采集</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="4">
                  <!-- 设备类型 -->
                  <a-form-model-item label="设备类型" prop="deviceType">
                    <a-select
                      v-model="entity.deviceType"
                      placeholder="请选择设备类型"
                      @change="handleDeviceTypeChange"
                    >
                      <a-select-option value="温度传感器">温度传感器</a-select-option>
                      <a-select-option value="湿度传感器">湿度传感器</a-select-option>
                      <a-select-option value="压力传感器">压力传感器</a-select-option>
                      <a-select-option value="振动传感器">振动传感器</a-select-option>
                      <a-select-option value="位移传感器">位移传感器</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="4">
                  <!-- 采集间隔 -->
                  <a-form-model-item 
                    label="采集间隔" 
                    prop="interval"
                    :rules="intervalRules"
                  >
                    <a-input
                      v-model="entity.interval"
                      placeholder="例如：5分钟"
                      :disabled="!isIntervalEnabled"
                    />
                  </a-form-model-item>
                </a-col>
                <a-col :span="4">
                  <!-- 优先级 -->
                  <a-form-model-item label="优先级" prop="priority">
                    <a-select v-model="entity.priority" placeholder="请选择优先级">
                      <a-select-option value="高">高</a-select-option>
                      <a-select-option value="中">中</a-select-option>
                      <a-select-option value="低">低</a-select-option>
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <!-- 触发条件 -->
                  <a-form-model-item 
                    label="触发条件" 
                    prop="triggerCondition"
                    :rules="triggerRules"
                  >
                    <a-input
                      v-model="entity.triggerCondition"
                      placeholder="请输入触发条件"
                      :disabled="!isTriggerEnabled"
                    />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </template>
          </g-collapse>
        </div>
      </template>
      <template #card-body>
        <g-collapse headerName="基础数据采集">
          <template #content>
            <g-data-points
              :dataPoints="entity.dataPoints"
              validDataName="dataPoints"
            >
            </g-data-points>
          </template>
        </g-collapse>
        <g-collapse headerName="设备状态数据">
          <template #content>
            <g-data-points
              :dataPoints="entity.statusDataPoints"
              validDataName="statusDataPoints"
            >
            </g-data-points>  
          </template>
        </g-collapse>
        <g-collapse headerName="数据处理配置">
          <template #content> 
            <a-row :gutter="24">
              <a-col :span="6">
                <!-- 缓存策略 -->
                <a-form-model-item label="缓存策略" prop="cacheStrategy">
                  <a-select v-model="entity.cacheStrategy" placeholder="请选择缓存策略">
                    <a-select-option value="不缓存">不缓存</a-select-option>
                    <a-select-option value="缓存最新">缓存最新</a-select-option>
                    <a-select-option value="缓存所有">缓存所有</a-select-option>
                  </a-select>
                </a-form-model-item>
              </a-col>
              <a-col :span="6">
                <!-- 重试次数 -->
                <a-form-model-item label="重试次数" prop="retryCount">
                  <a-input-number
                    v-model="entity.retryCount"
                    :min="0"
                    :max="10"
                    style="width: 100%"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :span="6">
                <!-- 重试间隔 -->
                <a-form-model-item label="重试间隔(秒)" prop="retryInterval">
                  <a-input-number
                    v-model="entity.retryInterval"
                    :min="1"
                    :max="60"
                    style="width: 100%"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :span="6">
                <!-- 超时时间 -->
                <a-form-model-item label="超时时间(秒)" prop="timeout">
                  <a-input-number
                    v-model="entity.timeout"
                    :min="1"
                    :max="300"
                    style="width: 100%"
                  />
                </a-form-model-item>
              </a-col>
            </a-row>
            <a-row :gutter="24">
              <a-col :span="24">
                <a-form-model-item label="数据处理方法" prop="dataProcessing">
                  <a-textarea
                    v-model="entity.dataProcessing"
                    placeholder="请输入数据处理方法"
                    :rows="4"
                    :max-length="1000"
                  ></a-textarea>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
        <g-collapse headerName="操作描述">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="12">
                <a-form-model-item name="description" label="描述">
                  <a-textarea
                    rows="4"
                    name="description"
                    v-model="entity.description"
                    placeholder="请输入采集任务描述"
                    :max-length="1000"
                  ></a-textarea>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </template>
      <template #g-fixed-row>
        <div class="g-fixed-row">
          <g-btn size="large" text="刷新" @callback="refresh()"></g-btn>
          <g-btn size="large" text="提交" type="primary" @callback="submitDataCollection()">
          </g-btn>
        </div>
      </template>
    </g-body-form-layout>
  </a-form-model>
</template>

<script>
import GSignModal from '@/components/g-sign-modal/g-sign-modal.vue';
import { createModal } from '@/utils';
import AppConst from '@/utils/appConst';

export default {
  name: "ResourceDataCollection",

  data() {
    return {
      entity: {
        deviceName: "",
        collectionType: "",
        deviceType: "",
        interval: "",
        priority: "中",
        triggerCondition: "",
        cacheStrategy: "不缓存",
        retryCount: 3,
        retryInterval: 5,
        timeout: 30,
        dataProcessing: "",
        description: "",
        dataPoints: [
          {
            dataValue: "",
            dataPointName: "温度值",
            isRequired: true,
            dataType: "Float",
            unit: "℃"
          },
          {
            dataValue: "",
            dataPointName: "湿度值",
            isRequired: false,
            dataType: "Float",
            unit: "%"
          },
          {
            dataValue: "",
            dataPointName: "采集时间",
            isRequired: true,
            dataType: "Timestamp"
          }
        ],
        statusDataPoints: [
          {
            dataValue: "",
            dataPointName: "设备运行状态",
            isRequired: true,
            dataType: "Boolean",
            booleanTrue: '在线',
            booleanFalse: '离线'
          },
          {
            dataValue: "",
            dataPointName: "设备电池电量",
            isRequired: false,
            dataType: "Integer",
            unit: "%"
          },
          {
            dataValue: "",
            dataPointName: "异常状态码",
            isRequired: false,
            dataType: "String"
          }
        ]
      },
      rules: {
        deviceName: [
          { required: true, message: "请选择设备", trigger: "change" }
        ],
        collectionType: [
          { required: true, message: "请选择采集类型", trigger: "change" }
        ],
        deviceType: [
          { required: true, message: "请选择设备类型", trigger: "change" }
        ],
        priority: [
          { required: true, message: "请选择优先级", trigger: "change" }
        ]
      },
      intervalRules: [
        { required: true, message: "请输入采集间隔", trigger: "blur" },
        { pattern: /^\d+[分秒]钟?$/, message: "格式必须为数字+分钟或数字+秒", trigger: "blur" }
      ],
      triggerRules: [
        { required: true, message: "请输入触发条件", trigger: "blur" }
      ],
      isIntervalEnabled: false,
      isTriggerEnabled: false,
      btnList: [
          {
            icon: AppConst.icon.documentShow,
            text: AppConst.button.documentShow,
            eventName: AppConst.button.documentShow,
            display: true
          }
        ],
    };
  },

  watch: {
    "entity.deviceName": {
      handler() {
        this.$refs.form.validateField("deviceName");
      },
    }
  },

  mounted() {
    // 默认值设置
    this.entity.collectionType = "定时采集";
    this.entity.deviceType = "温度传感器";
    this.handleCollectionTypeChange(this.entity.collectionType);
  },

  methods: {
    submitDataCollection() {
      this.$refs.form.validate().then((valid) => {
        if (!valid) {
          return;
        }
        this.createSignModal();
      });
    },
    // 创建签名弹窗
    createSignModal() {
      createModal({
        title: '签名',
        width: '600px',
      },{
        component: GSignModal,
        success: (v) => {
          console.log(v);
          // 处理提交成功后的逻辑
          this.$message.success('数据采集任务提交成功');
          this.refresh();
        },
      },);
    },
    // 刷新
    refresh() {
      // 重置表单
      this.$refs.form.resetFields();
      this.entity.collectionType = "定时采集";
      this.entity.deviceType = "温度传感器";
      this.entity.priority = "中";
      this.entity.cacheStrategy = "不缓存";
      this.entity.retryCount = 3;
      this.entity.retryInterval = 5;
      this.entity.timeout = 30;
      this.handleCollectionTypeChange(this.entity.collectionType);
    },
    // 处理采集类型变更
    handleCollectionTypeChange(value) {
      console.log("采集类型变更:", value);
      
      // 根据采集类型启用/禁用相关字段
      this.isIntervalEnabled = value === '定时采集' || value === '实时采集';
      this.isTriggerEnabled = value === '触发采集' || value === '条件采集';
      
      // 设置默认值
      if (this.isIntervalEnabled) {
        this.entity.interval = this.entity.interval || '5分钟';
        this.entity.triggerCondition = '';
      } else if (this.isTriggerEnabled) {
        this.entity.interval = '';
        this.entity.triggerCondition = this.entity.triggerCondition || '温度 > 30';
      } else {
        this.entity.interval = '';
        this.entity.triggerCondition = '';
      }
    },
    // 处理设备类型变更
    handleDeviceTypeChange(value) {
      console.log("设备类型变更:", value);
      
      // 根据设备类型调整数据点
      this.updateDataPointsByDeviceType(value);
    },
    // 根据设备类型更新数据点
    updateDataPointsByDeviceType(deviceType) {
      // 清空现有数据点
      this.entity.dataPoints = [];
      
      // 根据设备类型添加对应的数据点
      switch(deviceType) {
        case '温度传感器':
          this.entity.dataPoints.push(
            {
              dataValue: "",
              dataPointName: "温度值",
              isRequired: true,
              dataType: "Float",
              unit: "℃"
            },
            {
              dataValue: "",
              dataPointName: "环境温度",
              isRequired: false,
              dataType: "Float",
              unit: "℃"
            }
          );
          break;
        case '湿度传感器':
          this.entity.dataPoints.push(
            {
              dataValue: "",
              dataPointName: "湿度值",
              isRequired: true,
              dataType: "Float",
              unit: "%"
            },
            {
              dataValue: "",
              dataPointName: "露点温度",
              isRequired: false,
              dataType: "Float",
              unit: "℃"
            }
          );
          break;
        case '压力传感器':
          this.entity.dataPoints.push(
            {
              dataValue: "",
              dataPointName: "压力值",
              isRequired: true,
              dataType: "Float",
              unit: "kPa"
            },
            {
              dataValue: "",
              dataPointName: "压力变化率",
              isRequired: false,
              dataType: "Float",
              unit: "kPa/s"
            }
          );
          break;
        case '振动传感器':
          this.entity.dataPoints.push(
            {
              dataValue: "",
              dataPointName: "振动频率",
              isRequired: true,
              dataType: "Float",
              unit: "Hz"
            },
            {
              dataValue: "",
              dataPointName: "振动幅度",
              isRequired: true,
              dataType: "Float",
              unit: "mm"
            }
          );
          break;
        case '位移传感器':
          this.entity.dataPoints.push(
            {
              dataValue: "",
              dataPointName: "位移值",
              isRequired: true,
              dataType: "Float",
              unit: "mm"
            },
            {
              dataValue: "",
              dataPointName: "位移速度",
              isRequired: false,
              dataType: "Float",
              unit: "mm/s"
            }
          );
          break;
      }
      
      // 添加通用数据点
      this.entity.dataPoints.push({
        dataValue: "",
        dataPointName: "采集时间",
        isRequired: true,
        dataType: "Timestamp"
      });
    }
  },
};
</script>

<style lang="scss" scoped></style>